<template>
    <!--begin::Modal-->
    <div class="modal fade" id="transfareModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
        <div class="modal-dialog modal-dialog-centered" role="document">
            <form @submit.prevent="form.id ? update() : store()" class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="transfareModalLabel">{{form.id ?'تعديل':'تحويل'}}</h5>
                    <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close">
                        </button>
                </div>
                <div class="modal-body row" v-if="users&&users.length">
                    <div class="col-6 mt-3">
                        <label>المرسل</label>
                        <v-select dir="rtl" label="name" class="vselect" style="width: 100%;" :options="users" :reduce="val => val.id" v-model="form.sender_id"></v-select>
                    </div>
                    <div class="col-6 mt-3">
                        <label>المستلم</label>
                        <v-select dir="rtl" label="name" class="vselect" style="width: 100%;" :options="users" :reduce="val => val.id" v-model="form.user_id"></v-select>
                    </div>
                    <div class="col-12 mt-3">
                        <label>المبلغ</label>
                        <input v-model="form.money" type="text" autocomplete="off" class="form-control" :class="{ 'is-invalid': form.errors.has('money') }">
                        <has-error :form="form" field="money"></has-error>
                    </div>
                </div>
                <div class="modal-footer">
                    <button type="button" class="btn btn-light" data-bs-dismiss="modal">غلق</button>
                    <button v-if="form.id" type="submit" class="btn btn-primary">تعديل</button>
                    <button v-else type="submit" class="btn btn-primary">تحويل</button>
                </div>
            </form>
        </div>
    </div>
    <!--end::Modal-->
</template>
<script>
import { Form, HasError } from 'vform'
import axios from 'axios';
import "vue-select/dist/vue-select.css";
export default {
    components: { HasError },
    props: ['fetchData'],
    data() {
        return {
            form: new Form({
                id: null,
                user_id: null,
                money: null,
            }),
            users: null,
        };
    },
    created() {
        this.getInfo();
    },
    methods: {
        create() {
            this.form.reset();
            $('#transfareModal').modal('show');
        },
        getInfo() {
            axios.get("/api/users", { params: { paginate: 0 } }).then(({ data }) => {
                this.users = data.success.users;
            }).catch((error) => { console.log("Error......") })
        },
        store() {
            this.form.post("/api/transfares").then(({ data }) => {
                if (data.message) {
                    this.$swal.fire({ icon: 'error', title: "Error Message", text: data.message + '!' });
                } else {
                    this.fetchData();
                    $('#transfareModal').modal('hide');
                }
            }).catch((error) => { console.log("Error......") })
        },
        edit(form) {
            this.form.fill(form);
            $('#transfareModal').modal('show');
        },
        update() {
            this.form.put("/api/transfares/" + this.form.id).then((res) => {
                this.fetchData();
                $('#transfareModal').modal('hide');
            }).catch((error) => { console.log("Error......") })
        },
        destroy(id) {
            this.$swal.fire({
                title: this.$t("general.warning"),
                text: this.$t("general.r_u_s"),
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: this.$t("general.cancel"),
                confirmButtonText: this.$t("general.yes_delete"),
            }).then((result) => {
                if (result.value) {
                    axios.delete('/api/transfares/' + id).then(response => {
                        if (response.data.message) {
                            this.$swal.fire({ icon: 'error', title: 'Oops...', text: response.data.message + '!' });
                        } else {
                            this.$swal.fire(this.$t("general.deleted"), this.$t("general.delete_success"), 'success');
                            this.fetchData();
                        }
                    }).catch(() => {
                        this.$swal.fire({ icon: 'error', title: 'Oops...', text: 'Something went wrong!' });
                    });
                }
            })
        },
    },
}
</script>
